import { Component } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { v4 as uuidv4 } from 'uuid';
import FormControlHint from './FormControlHint';
import FormLabel from './FormLabel';
import InlineFormError from './InlineFormError';

// Styles
import './styles/floating-label-composer.scss';

const withFormField = (Comp) =>
    class FormField extends Component {
        static defaultProps = {
            rwSuffix: '_input',
            hasError: false,
            isDisabled: false,
            isReadOnly: false,
            shouldIncludeError: true,
            shouldIncludeHint: true,
            onChange: () => Promise.resolve(),
        };

        static propTypes = {
            contextCls: PropTypes.string,
            id: PropTypes.string,
            'aria-label': PropTypes.string,
            placeholder: PropTypes.string,
            value: PropTypes.string,
            rwSuffix: PropTypes.string,
            hasError: PropTypes.bool,
            isDisabled: PropTypes.bool,
            isReadOnly: PropTypes.bool,
            shouldIncludeError: PropTypes.bool,
            shouldIncludeHint: PropTypes.bool,
            errorProps: PropTypes.object,
            hintProps: PropTypes.object,
            labelProps: PropTypes.shape({
                labelText: PropTypes.oneOfType([
                    PropTypes.string,
                    PropTypes.element,
                ]),
            }),
            renderAfterControl: PropTypes.func,
            renderBeforeControl: PropTypes.func,
            onChange: PropTypes.func,
        };

        constructor(...args) {
            super(...args);

            const hasValue = this.getHasValue();
            this.state = { hasValue };
        }

        render() {
            const { contextCls } = this.props;
            const modCls = this.getModCls();
            const shouldIncludeHints = this.getShouldIncludeHints();

            return (
                <div className={cx(this.baseCls, modCls, contextCls)}>
                    <div className={`${this.baseCls}__inner`}>
                        {this.renderComp()}
                    </div>
                    {shouldIncludeHints && this.renderHints()}
                </div>
            );
        }

        componentDidUpdate(prevProps) {
            const { value: currValue } = this.props;
            const { value: prevValue } = prevProps;
            const hasValue = !!currValue;
            const isNewValue = currValue !== prevValue;

            this.synchronizeHasValue(hasValue, isNewValue);
        }

        renderComp = () => {
            const { renderBeforeControl, renderAfterControl } = this.props;
            const props = this.getCompExtantProps();
            const hasLabel = this.getHasLabel();

            return (
                <div className={`${this.baseCls}__content-container`}>
                    {!!renderBeforeControl && (
                        <div
                            className={`${this.baseCls}__before-comp-container`}
                        >
                            {renderBeforeControl(props)}
                        </div>
                    )}
                    <div className={`${this.baseCls}__control-container`}>
                        <div
                            className={`${this.baseCls}__control-container-inner`}
                        >
                            <Comp {...props} />
                            {hasLabel && this.renderLabel()}
                        </div>
                    </div>
                    {this.renderHalo()}
                    {!!renderAfterControl && (
                        <div
                            className={`${this.baseCls}__after-comp-container`}
                        >
                            {renderAfterControl(props)}
                        </div>
                    )}
                </div>
            );
        };

        renderLabel = () => {
            const props = this.getLabelExtantProps();

            return (
                <div className={`${this.baseCls}__label-container`}>
                    <FormLabel {...props} />
                </div>
            );
        };

        renderHalo = () => <div className={`${this.baseCls}__field-halo`} />;

        renderHints = () => {
            const { shouldIncludeError, shouldIncludeHint } = this.props;
            const isDisplayingHint = this.getHasHint() || this.getHasError();
            const hintBaseCls = `${this.baseCls}__hints-container`;
            const modCls = {
                [`${hintBaseCls}--is-displaying-hint`]: isDisplayingHint,
            };

            return (
                <div className={cx(hintBaseCls, modCls)}>
                    {shouldIncludeError && this.renderError()}
                    {shouldIncludeHint && this.renderHint()}
                </div>
            );
        };

        renderError = () => {
            const props = this.getErrorExtantProps();

            return <InlineFormError {...props} />;
        };

        renderHint = () => {
            const props = this.getHintExtantProps();

            return <FormControlHint {...props} />;
        };

        handleChange = (evt) => {
            const { onChange } = this.props;
            const newState = {};

            if (evt?.target?.value !== undefined) {
                const value = evt?.target?.value?.trim();
                newState.hasValue = value.length > 0;
            } else if (Array.isArray(evt)) {
                newState.hasValue = evt.length > 0;
            } else {
                newState.hasValue = !!evt;
            }

            this.setState(newState);

            if (onChange) {
                onChange(evt);
            }
        };

        getHasValue = () => {
            const { value } = this.props;

            // eslint-disable-next-line no-null/no-null
            return value !== undefined && value !== null && value !== '';
        };

        getModCls = () => {
            const { isDisabled, isReadOnly, hasError } = this.props;
            const { hasValue } = this.state;
            const hasLabel = this.getHasLabel();

            return {
                [`${this.baseCls}--is-disabled`]: isDisabled,
                [`${this.baseCls}--error`]: hasError,
                [`${this.baseCls}--is-read-only`]: isReadOnly,
                [`${this.baseCls}--has-value`]: hasValue,
                [`${this.baseCls}--no-label`]: !hasLabel,
            };
        };

        getErrorId = () => {
            const { id } = this.props;

            return `${id || this.id}-error`;
        };

        getHintId = () => {
            const { id } = this.props;

            return `${id || this.id}-hint`;
        };

        getCompARIADescByIds = () => {
            const { shouldIncludeHint, shouldIncludeError } = this.props;
            const hasLabel = this.getHasLabel();

            return [
                ...(shouldIncludeError ? [this.getErrorId()] : []),
                ...(shouldIncludeHint && hasLabel ? [this.getHintId()] : []),
            ].join(' ');
        };

        getErrorExtantProps = () => {
            const { errorProps } = this.props;

            return {
                ...errorProps,
                id: this.getErrorId(),
            };
        };

        getHintExtantProps = () => {
            const { hintProps } = this.props;
            const hasError = this.getHasError();
            const { hintText, renderHint, ...rest } = hintProps || {};

            return {
                ...rest,
                ...(!hasError && {
                    ...(hintText && { hintText }),
                    ...(renderHint && { renderHint }),
                }),
                id: this.getHintId(),
            };
        };

        getLabelExtantProps = () => {
            const { id, labelProps, rwSuffix } = this.props;

            return {
                ...labelProps,
                htmlFor: `${id || this.id}${rwSuffix}`,
            };
        };

        getShouldIncludeHints = () => {
            const { shouldIncludeError, shouldIncludeHint } = this.props;

            return shouldIncludeError || shouldIncludeHint;
        };

        getHasHint = () => {
            const { hintProps } = this.props;
            const { hintText, renderHint } = hintProps || {};

            return !!hintText || !!renderHint;
        };

        getHasError = () => {
            const { hasError, errorProps } = this.props;
            const { errorText, renderError } = errorProps || {};

            return hasError || !!errorText || !!renderError;
        };

        getHasLabel = () => {
            const { labelProps } = this.props;
            const { labelText, renderCustomLabel } = labelProps || {};

            return !!labelText || !!renderCustomLabel;
        };

        getCompExtantProps = () => {
            const {
                contextCls,
                'aria-label': ariaLabel,
                placeholder,
                rwSuffix,
                shouldIncludeError,
                shouldIncludeHint,
                errorProps,
                hintProps,
                labelProps,
                id,
                isReadOnly,
                hasError,
                renderBeforeControl,
                renderAfterControl,
                ...rest
            } = this.props;
            const hasCompError = this.getHasError();
            const hasLabel = this.getHasLabel();

            return {
                ...rest,
                id: `${id || this.id}${rwSuffix}`,
                ...(!hasLabel && { 'aria-label': ariaLabel, placeholder }),
                ...(isReadOnly && { isReadOnly }),
                ...(hasCompError && { hasError: hasCompError }),
                ...((shouldIncludeError || shouldIncludeHint) && {
                    'aria-describedby': this.getCompARIADescByIds(),
                }),
                onChange: this.handleChange,
            };
        };

        synchronizeHasValue = (hasValue, isNewValue) => {
            const newState = { hasValue: false };

            if (isNewValue) {
                if (hasValue) {
                    newState.hasValue = true;
                }

                this.setState(newState);
            }
        };

        baseCls = 'eureka-floating-label-composer';

        id = `eureka-flf-${uuidv4()}`;
    };

const FloatingLabelComposer = withFormField;

export default FloatingLabelComposer;
