import React from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { SVGWrapper } from '@Green-Dot-Corporation/eureka-ui-media';

// Constants
import baseCls from './const/baseClsConst';

const IconSVGWrapper = (props) => {
    const { contextCls, title, strokeWidth, fill, viewBox, children, ...rest } =
        props;

    return (
        <SVGWrapper
            {...rest}
            className={cx(baseCls, contextCls)}
            width="1em"
            height="1em"
            stroke="currentColor"
            fill={fill}
            strokeWidth={strokeWidth}
            viewBox={viewBox}
        >
            {title && <title>{title}</title>}
            {children}
        </SVGWrapper>
    );
};

IconSVGWrapper.defaultProps = {
    fill: 'currentColor',
    viewBox: '0 0 24 24',
    strokeWidth: 0,
};

IconSVGWrapper.propTypes = {
    contextCls: PropTypes.string,
    fill: PropTypes.string,
    title: PropTypes.string,
    viewBox: PropTypes.string,
    strokeWidth: PropTypes.number,
};

export default IconSVGWrapper;
